/* src/app/check-out/check-out.css */
.container {
  max-width: 960px;
}
.card {
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  border: none;
}
.form-check-input:checked {
  background-color: #0d6efd;
  border-color: #0d6efd;
}
.btn-primary {
  background-color: #0d6efd;
  border-color: #0d6efd;
  border-radius: 0.75rem;
  transition: all 0.3s ease;
}
.btn-primary:hover {
  background-color: #0b5ed7;
  border-color: #0a58ca;
  transform: translateY(-2px);
}
.list-group-item {
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
}
.form-control {
  border-radius: 0.5rem;
}
.order-summary-card {
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  background: #fff;
  padding: 20px;
}
.order-summary-card h4 {
  font-size: 1.2rem;
  font-weight: 600;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}
.list-group-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: none;
  border-bottom: 1px solid #f1f1f1;
  padding: 12px 0;
}
.list-group-item:last-child {
  border-bottom: none;
}
.list-group-item img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  margin-right: 12px;
  border-radius: 10px;
}
.list-group-item h6 {
  font-size: 0.95rem;
  margin-bottom: 4px;
  font-weight: 600;
}
.list-group-item small {
  color: #666;
  font-size: 0.85rem;
}
.price-box {
  min-width: 100px;
  text-align: right;
  font-size: 0.95rem;
  font-weight: 600;
  color: red;
  padding: 6px 10px;
  background: #f9f9f9;
  border-radius: 8px;
}
